<template>
    <div class="dataGroup noSelect">
        <div class="item">
            <h1>{{ datalist.usercount | format }}</h1>
            <p>总用户 / 人</p>
        </div>
        <div class="item">
            <h1>{{ datalist.appcount | format }}</h1>
            <p>总应用 / 个</p>
        </div>
        <div class="item" v-link="{ name: 'DAAffair' }">
            <h1>{{ datalist.issuecount | format }}</h1>
            <p>总事务 / 个</p>
        </div>
        <div class="item">
            <h1>{{ datalist.feewarncount | format }}</h1>
            <p>电费提醒 / 个订阅</p>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            datalist: {
                usercount: 0,
                appcount: 0,
                issuecount: 0,
                feewarncount: 0
            }
        }
    },
    filters: {
        format (s) {
            s += ''
            let l = s.split('').reverse()
            let t = ''
            for (let i = 0; i < l.length; i++) {
                t += l[i] + ((i + 1) % 3 === 0 && (i + 1) !== l.length ? ',' : '')
            }
            return t.split('').reverse().join('')
        }
    },
    ready () {
        if (window.location.hostname !== 'localhost') {
            this.$http.get('http://szdx.sinaapp.com/dashboard/rest/v1/datalist.php').then(res => {
                this.datalist = res.data
            })
        }
    }
}
</script>

<style lang="less" scoped>
.dataGroup {
    width: 90%;
    height: 140px;
    margin: 5% auto;
    border-radius: 5px;
    background: #408ec0;
    .item {
        float: left;
        width: 25%;
        padding: 30px;
        text-align: center;
        cursor: pointer;
        border-right: 1px solid rgba(0, 0, 0, .03);
        h1 {
            color: rgba(255, 255, 255, .99);
            font-size: 40px;
            line-height: 50px;
            font-weight: normal;
            font-family: "Helvetica Neue","Hiragino Sans GB","Microsoft YaHei","\9ED1\4F53",Arial,sans-serif;
        }
        p {
            color: rgba(255, 255, 255, .95);
            font-size: 14px;
            line-height: 30px;
        }
        &:hover {
            background: rgba(0, 0, 0, .05);
        }
    }
}
</style>



